import { SubmitHandler, useForm } from "react-hook-form";
import { Link } from "react-router-dom";
import { yupResolver } from "@hookform/resolvers/yup";

import { schema } from "../lib";
import { FormInputsProps } from "shared/const/types";
import { Button } from "shared/ui/Button/Button";

import {
	MobileText,
	Text,
	BtnsWrapper,
	HeaderWrapper,
	StyledButton,
	StyledInput,
	StyledLink,
	Header,
	Form,
} from "./styles";
import { ToastModel } from "entities/Toast";

export const FormPasswordRecovery = () => {
	const {
		register,
		handleSubmit,
		formState: { errors },
	} = useForm<FormInputsProps>({
		mode: "onChange",
		resolver: yupResolver(schema),
	});

	const onSubmit: SubmitHandler<FormInputsProps> = (data) => {
		ToastModel.successToast(false);
		ToastModel.setToast({
			textError: "   .",
			titleSuccess: " ",
			textSuccess: "",
		});
		ToastModel.showToast(true);
	};

	return (
		<Form onSubmit={handleSubmit(onSubmit)}>
			<HeaderWrapper>
				<StyledLink to="/login"></StyledLink>
				<Header> </Header>
			</HeaderWrapper>
			<StyledInput
				register={register}
				id="email"
				label=" "
				placeholder="  . "
				errors={errors.email}
				errorMsg={errors.email?.message}
			/>
			<BtnsWrapper>
				<Button>
					<Text> </Text>
					<MobileText></MobileText>
				</Button>
				<Link to="/login">
					<StyledButton></StyledButton>
				</Link>
			</BtnsWrapper>
		</Form>
	);
};
