import * as yup from "yup";
import { Regulars } from "shared/const/regExp";

export const schema = yup.object().shape({
	email: yup
		.string()
		.matches(Regulars.email, "   ")
		.required("  "),
	password: yup
		.string()
		.matches(
			Regulars.password,
			'     8 :     ,     : "!#$%&"',
		)
		.required("  "),
});
