import { Regulars } from "shared/const/regExp";
import * as yup from "yup";

export const schema = yup.object().shape({
	firstName: yup
		.string()
		.min(2, "     2 ")
		.max(40, "     40 ")
		.matches(
			Regulars.name,
			"       ",
		),
	lastName: yup
		.string()
		.min(2, "     2 ")
		.max(40, "     40 ")
		.matches(
			Regulars.name,
			"       ",
		),
	file: yup
		.mixed()
		.test(
			"fileSize",
			" .    5Mb.",
			value => {
				if (value && value.length > 0) {
					return value[0]?.size <= 1024 * 1024 * 5;
				}
				return true;
			},
		),
	dateBirth: yup
		.string()
		.matches(Regulars.date, "    : ..")
		.test(
			"date",
			"     ",
			(value: any) => {
				const today = new Date().getTime();
				const parts = value.split(".");
				const birthDate = new Date(
					`${parts[1]}-${parts[0]}-${parts[2]}`,
				).getTime();

				return birthDate <= today;
			},
		),
	city: yup.object({
		label: yup.string(),
		value: yup.string(),
	}),
	sex: yup.object({
		label: yup.string(),
		value: yup.string(),
	}),
	pet: yup.object({
		label: yup.string(),
		value: yup.string(),
	}),
	information: yup
		.string()
		.max(99, "   - 99 "),
	about: yup.string().max(300, "   - 300 "),
});
