import * as yup from "yup";
import { Regulars } from "shared/const/regExp";

export const schema = yup.object().shape({
	name: yup
		.string()
		.min(4, "     4 ")
		.max(64, "     64 ")
		.matches(Regulars.name, "      ")
		.required("    "),
	text: yup
		.string()
		.min(6, "     6 ")
		.max(200, "   - 200 ")
		.required("   "),
	captcha: yup
		.string()
		.min(5, "   5 ")
		.max(5, "   5 ")
		.required("  "),
	file: yup
		.mixed()
		.test(
			"fileSize",
			" .    5Mb.",
			value => {
				if (value && value.length > 0) {
					return value[0]?.size <= 1024 * 1024 * 5;
				}
				return true;
			},
		),
});
